@import url("https://fonts.googleapis.com/css?family=Lato:400,700,900&display=swap");

body {
    background-color: #e7ecf2;
}

.profile {
    width: 268px;
    border-radius: 4px;
    filter: drop-shadow(0px 1px 1.5px rgba(191, 205, 222, 0.75));
    background-color: #ffffff;
    margin: auto;
}

.description {
    padding-top: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.avatar {
    width: 99px;
    height: 99px;
    border-radius: 49px;
    background-color: #000000;
    margin-bottom: 30px;
}

.name {
    font-weight: 700;
    font-size: 18px;
    margin-top: 0;
    padding: 0;
    color: #122236;
    margin-bottom: 8px;
}

.tag {
    margin-top: 0;
    padding: 0;
    font-size: 14px;
    color: #768696;
    margin-bottom: 8px;
}

.location {
    margin-top: 0;
    padding: 0;
    font-size: 14px;
    color: #768696;
    margin-bottom: 30px;
}

.stats {
    padding: 0;
    margin: 0;
    background-color: #f3f6f9;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    list-style: none;

    border-bottom-left-radius: 3px;
    border-bottom-right-radius: 3px;
    outline: 1px solid #e4e9f0;
}

.stats li {
    width: 84px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    border-right: 1px solid #e4e9f0;
}

.stats :last-child {
    border-right: none;
}

.label {
    padding-top: 18px;
    margin-bottom: 8px;
    font-family: "Lato";
    font-size: 12px;
    color: #768696;
}

.quantity {
    font-family: "Lato";
    font-size: 14px;
    font-weight: 900;
    color: #1f3349;
    margin-bottom: 24px;
}