.friendList {
    padding: 0;
    width: 350px;
    margin: auto;
    display: flex;
    flex-direction: column;
    list-style-type: none;
    padding-top: 10px;
    margin-bottom: 15px;
}

.item {
    margin: 0;
    padding: 12px 0 12px 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 4px;
    background-color: #fff;
}

.status {
    display: block;
    width: 26px;
    height: 26px;
    border-radius: 50%;
    margin-left: 12px;
    margin-right: 12px;
}

.online {
    composes: status;
    background-color: green;
}

.offline {
    composes: status;
    background-color: red;
}

.avatar {
    width: 100px;
    height: auto;
    border-radius: 6px;
}

.name {
    font-size: 24px;
    font-family: "Montserrat";
    font-weight: bold;
    padding-left: 15px;
}